.container {
  display: flex;
  min-height: 100%;
  max-width: 100%;
  padding-top: 10px;
  padding-left: 100px;
  padding-right: 100px;
  padding-right: 100px;
  padding-left: 100px;
  flex-direction: row;
  flex: 1;
}
label {
  font-size: 20px;
  width: 100px;
  display: inline-block;
}
.form{
  padding: 0px;
  margin: 0px;
  min-height: 800px;
}
#reviewText, #usernameText{
  width: 265px;
  max-width: 100%;
  font-size: 15px;
  background-color: #bbb1b1;
  border-style: none;
}
#reviewText {
  min-height: 550px;
}
.box {
  margin: 5px;
  padding: 5px;
  /* border: 1px solid #fff; */
  flex: 1;
}
.container {
  background-color: black;
}
.inner-col {
  flex: 1;
  flex-direction: row;
  max-width: 100%;
  min-height: 500px;
  display: flex;
}
#article {
  color: black;
}
.desc {
  max-width: 100%;
  min-height: 95%;
  border-left: 2px solid white;
}
.desc > img {
  /* opacity: 0.7; */
  width: 600px;
}
#submitBtn {
  max-width: 100px;
  margin: 5px;
  padding: 5px;
  background-color: #ffc31f;
  border-radius: 5px;
  font-family: "Titillium Web", "Kdam Thmor Pro";
  cursor: pointer;
}
.inner-col2 {
  flex: 1;
  max-width: 25%;
}
.out-image {
  max-width: 35%;
  min-height: 500px;
  flex-direction: column;
}
.abov-image {
  max-width: 100%;
  min-height: 350px;
  border-bottom: 2px solid white;
}
.und-image {
  max-width: 100%;
  min-height: 110px;
}
.reviewBox, .reviewsBox {
  border-bottom: 2px solid #ffc31f;
}
#footer, #aside, .innerContainer {
  background-color: #1f1c1c;
}
.inner-col {
  background-color: #ffc31f;
}
.table {
  flex-direction: row;
  flex: 1;
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid white;
}
#movieStars{
  color: white;
}